import React from "react"
import {MdxTemplateProps} from "./MdxTemplate";
import "../styles.scss";
import {MDXProvider} from "@mdx-js/react";
import ItemLink from "../components/ItemLink";
import RecipeFor from "../components/RecipeFor";

/**
 * These components are automatically available in all Markdown files.
 */
const shortcodes = {ItemLink, RecipeFor};

function PageTemplate(props: MdxTemplateProps) {
    const {children, pageContext: {frontmatter}} = props;
    return (
        <main className="content">
            <div className="container">
                <MDXProvider components={shortcodes}>{children}</MDXProvider>
            </div>
        </main>
    );
}

export default PageTemplate;
